:root {
  --background-color2: rgba(191, 237, 233, 0.5);
  --tweetBox: #edeae5;
  --tweet: #fef9c7;
}
body {
  margin: 0;
  font-family: "Ubuntu", sans-serif, "Trebuchet MS", Helvetica, sans-serif;
}
#root {
  background-color: rgba(191, 237, 233, 0.5);
  background-color: var(--background-color2);
  min-height: 100vh;
}

.container {
  padding-top: 50px;
  display: -webkit-flex;
  display: flex;
  width: 70%;
  margin: 0 auto;
}
.tweets-container {
  -webkit-flex-basis: 70%;
          flex-basis: 70%;
  padding: 30px 5px 30px 5px;
  margin: 0 20px 0 20px;
  background-color: #edeae5;
  background-color: var(--tweetBox);
  border-radius: 10px;
  box-shadow: 1px 1px 1px lightgrey;
}
.post-container {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  position: relative;
}
#post {
  width: 50%;
  border-radius: 5px;
  border: none;
  padding: 10px;
  box-shadow: 1px 1px 1px lightgrey;
  transition: 0.5s width ease-in;
}
.post-label {
  position: absolute;
  -webkit-transform: translateY(50%);
          transform: translateY(50%);
  transition: -webkit-transform 0.5s ease-in;
  transition: transform 0.5s ease-in;
  transition: transform 0.5s ease-in, -webkit-transform 0.5s ease-in;
  white-space: nowrap;
  padding-left: 10px;
  cursor: text;
}
#post:focus {
  width: 100%;
}
#post:focus + .post-label {
  -webkit-transform: translateY(-125%);
          transform: translateY(-125%);
  cursor: default;
}
.post-button {
  -webkit-flex-basis: 10%;
          flex-basis: 10%;
}
#username-input {
  display: block;
  margin: 0 auto;
  width: 30%;
  text-align: center;
  border-radius: 5px;
  border: none;
  padding: 10px;
  box-shadow: 1px 1px 1px lightgrey;
  transition: 0.5s width ease-in;
}
#username-input:focus {
  width: 80%;
}
#username-input:focus + .username-input-label {
  top: 25%;
  cursor: default;
}
.username-input-container {
  position: relative;
  padding-top: 50px;
}
.username-input-label {
  position: absolute;
  top: 80%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  transition: top 0.5s ease-in;
  cursor: text;
  width: 100%;
}
.login-container {
  text-align: center;
  -webkit-animation: animation 1s ease-in;
          animation: animation 1s ease-in;
}
@-webkit-keyframes animation {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes animation {
  0% {
    opacity: 0;
  }
  90% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.logo-container {
  max-width: 300px;
  max-height: 300px;
  margin: 0 auto;
}
.logo-container img {
  max-width: 300px;
}
.profile-container {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  -webkit-align-self: start;
          align-self: start;
  padding: 20px;
  background-color: #edeae5;
  border-radius: 10px;
  box-shadow: 1px 1px 1px lightgrey;
  position: -webkit-sticky;
  position: sticky;
  top: 50px;
}
